	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-07-02-scalr-scalable-hosting-framework-for-amazon-web-services/index.md absPath of file >>> MarkdownRemark","html":"<p>Amazon's web services business has sparked a number of business opportunities. Using EC2 and S3 is not especially easy for a beginner, but scalable web architectures aren't a place for the weak hearted geek.</p>\n<p>Enter <a href=\"http://code.google.com/p/scalr/\">Scalr</a>. This is an open source project meant to make building a scalable, fault tolerant system on Amazon's services easier.</p>\n<p>If that is still too hard then take a look at <a href=\"https://www.scalr.net/login.php\">scalr.net</a> a service that for $50 a month will manage your scalr instance for you. (You still have to pay all the applicable Amazon charges.</p>\n<p>All this comes to us from <a href=\"http://www.intridea.com/\">Intridea</a> which also produces <a href=\"http://socialspring.com\">SocialSpring</a> the platform behind <a href=\"http://www.actsascommunity.com/\">Acts as Community</a>.</p>","frontmatter":{"title":"Scalr &#8211; Scalable Hosting Framework for Amazon Web Services","date":"July 02, 2008","imageUrl":null,"image":null}}},"pathContext":{"path":"/scalr-scalable-hosting-framework-for-amazon-web-services"}}

